import { mapState,mapGetters } from 'vuex';
export default {
	data() {
		return {
			person_list:[]
		}
	},
	
	components:{
	},
	mounted() {
		this.get_people_list()
	},
	computed: {
		...mapState(['bgt_c_task','bgt_ct_id']),
		...mapGetters(['user_role','id']),
	},
	methods: {
		get_people_list(){
			this.$http.get('personwx.headpersonlist/1.0/',{
				proId:this.bgt_ct_id,
				pid:this.id
			}).then(res =>{
				console.log(res)
				this.person_list = this.$utils._get(res,'data.data',[])
			})
		},
		status_text(status){
			switch (status){
				case '0':
					return '确认不来了'
					break;
				case '1':
					return '待确认'
					break;	
				case '2':
					return '确认工作'
					break;	
				case '3':
					return '下工'
					break;	
				default:
					break;
			}
		},
		status_color(status){
			switch (status){
				case '0':
					return 'line-red'
					break;
				case '1':
					return 'line-blue'
					break;	
				case '2':
					return 'line-green'
					break;	
				case '3':
					return 'line-gray'
					break;	
				default:
					break;
			}
		},
		goto_detail(item){
			uni.navigateTo({
				url: `/pages/tabbar/workbench/peopleDetail/peopleDetail?item=` + encodeURIComponent(JSON.stringify(item))
			});
		}
	}
}
